const P2MR_WITNESS_VERSION = 2;
const P2MR_DATA_LENGTH = 32;
function isP2MRAddress(addr, network) {
  try {
    const decoded = fromBech32(addr);
    return decoded.prefix === network.bech32 && decoded.version === P2MR_WITNESS_VERSION && decoded.data.length === P2MR_DATA_LENGTH;
  } catch {
    return false;
  }
}

